.sidebar {
    background-color:rgb(237 237 237);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
  }
  
  .sidebar > a:first-child {
    padding: 0;
  }
  .sidebar > a > img {
    width: 100%;
    transition: all 0.5s;
  }
  
  .sidebar > a > img:hover {
    filter: drop-shadow(0 0 10px tomato);
  }
  .sidebar a {
    text-decoration: none;
    color: #1976d2;;
    font: 200 1vmax "Roboto";
    font-weight: bolder;
    padding: 2vmax;
    transition: all 0.5s;
  }
  .sidebar a:hover {
    color:  black;
    transform: scale(1.1);
  }
  
  .sidebar a > P {
    display: flex;
    align-items: center;
    

  }
  .sidebar a > p > svg {
    margin-right: 0.5rem;
  }
  



  